import { mapState, mapActions } from 'vuex'
import { upyunUrl } from '../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      oJson: '',
      schoolVal: '',
      cultureVal: '',
      specialtyVal: '',
      initialStatus: false,
      dateStart: '',
      dateEnd: ''
    }
  },
  onLoad (options) {
    const data = JSON.parse(options.data)
    this.oJson = data
    this.initialStatus = false
    this.schoolVal = data.school
    this.cultureVal = data.culture
    this.specialtyVal = data.specialty
    this.dateStart = data.start_time
    this.dateEnd = data.end_time
  },
  onShow () {
    if (!this.oJson) this.onEmptyFn()
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapActions([
      'isEducationInfo'
    ]),
    // 选择开始日期
    bindDateChange (e) {
      this.dateStart = e.target.value
    },
    // 选择结束日期
    bindDateEndChange (e) {
      this.dateEnd = e.target.value
    },
    // 清空数据
    onEmptyFn () {
      this.schoolVal = ''
      this.cultureVal = ''
      this.specialtyVal = ''
      this.dateStart = ''
      this.dateEnd = ''
    },
    // 保存工作经历
    onSaveFn () {
      if (this.initialStatus) return false
      const schoolVal = this.schoolVal
      const cultureVal = this.cultureVal
      const specialtyVal = this.specialtyVal
      const dateStart = this.dateStart
      const dateEnd = this.dateEnd
      const data = {
        that: this,
        schoolVal,
        cultureVal,
        specialtyVal,
        dateStart,
        dateEnd
      }
      if (!schoolVal) {
        this.showToastFn('学校不能为空!')
        return false
      }
      if (!cultureVal) {
        this.showToastFn('学历不能为空!')
        return false
      }
      if (!specialtyVal) {
        this.showToastFn('专业不能为空!')
        return false
      }
      if (!dateStart) {
        this.showToastFn('请选择开始时间!')
        return false
      }
      if (!dateEnd) {
        this.showToastFn('请选择结束时间!')
        return false
      }
      uni.showLoading({ title: 'Loading' })
      this.initialStatus = true
      this.isEducationInfo(data).then(() => {
        if (this.oJson) {
          this.showToastFn('修改成功')
        } else {
          this.showToastFn('添加成功')
        }
        setTimeout(() => {
          uni.navigateBack()
        }, 1500)
      })
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 1500
      })
    }
  },
  onUnload () {
    this.onEmptyFn()
  }
}
